<template>
    <div>
        <img
            v-show="!isCollect"
            class="star"
            src="../../assets/star.png"
            @click="addCollection"
        >
        <img
            v-show="isCollect"
            class="star"
            src="../../assets/star-a.png"
            @click="deleteCollection"
        >
    </div>
</template>

<script>
import { collectList, couponAddorDelete, getUserData } from '../../services/index'

export default {
    name: 'chat-list',
    props: {
        details: {
            type: Object,
            default () {
                return {}
            }
        }

    },
    data () {
        return {
            isCollect: false
        }
    },
    computed: {
        username () {
            return getUserData()
        }
    },
    watch: {
        'details.id' () {
            this.getCollect()
        }
    },
    methods: {
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        async getCollect () {
            console.log(this.details)
            if (this.username && await collectList(this.details.id)) this.isCollect = true
            else this.isCollect = false
        },
        async addCollection () {
            if (this.username) {
                await couponAddorDelete(this.details.id)
                this.getCollect()
            } else {
                this.toGetUser();
                window.webkit.messageHandlers.login.postMessage('');
            }
        },
        async deleteCollection () {
            await couponAddorDelete(this.details.id)
            this.getCollect()
        }
    }
}
</script>

<style scoped lang="scss">
.star {
  width: 45px;
}
</style>
