<template>
    <nacl-page-container id="details" no-header>
        <!-- <nacl-header>商品详情</nacl-header> -->
        <nacl-page-content>
            <div class="img">
                <img :src="imgSrc">
            </div>
            <div class="data">
                <p class="title">{{ details.title }}</p>
                <p class="lable">
                    <span>新品上架</span>
                    <span>下单抽奖</span>
                    <span>包邮</span>
                </p>
                <p class="pre">
                    ¥{{ details.price }}
                    <span v-if="price === '1'" class="spike-time">21点准点开始秒杀!</span>
                </p>
            </div>
            <div
                class="content"
                v-html=" details.details"
            />
            <footer>
                <nacl-flexbox
                    justify="between"
                    align="center"
                >
                    <div class="star">
                        <!-- <img src="../assets/star.png"> -->
                        <star v-if="details" :details="details" />
                        <p>收藏</p>
                    </div>
                    <basicButton
                        v-if="price !== '1'"
                        type="primary"
                        size="sm"
                        radius
                        class="addCart"
                        @click="addCart"
                    >
                        加入购物车
                    </basicButton>
                    <basicButton
                        v-if="price !== '1'"
                        type="primary"
                        size="sm"
                        radius
                        class="purchase"
                        @click="addOrder"
                    >
                        立即购买
                    </basicButton>
                    <basicButton
                        v-if="price === '1'"
                        type="primary"
                        size="sm"
                        radius
                        class="spike"
                        @click="$toast('秒杀还未开始')"
                    >
                        秒杀还未开始
                    </basicButton>
                </nacl-flexbox>
            </footer>
        </nacl-page-content>
    </nacl-page-container>
</template>

<script>
import basicButton from '../components/basic-button'
import star from '../components/star'
import { addCart, fastadd, getGoodsDetail, getUserData } from '../services'
// import axios from 'axios'

export default {
    name: 'details',
    components: { basicButton, star },
    data () {
        return {
            userData: getUserData(),
            id: this.$route.params.id,
            details: {},
            imgSrc: require('../assets/default.png'),
            price: this.$route.query.price
        }
    },
    created () {
        this.getDetails()
    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        },
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        async addCart () {
            if (this.userData) {
                await addCart({
                    userId: this.userData.userId,
                    goodsId: this.details.id,
                    number: 1
                })
                this.$toast('加入购物车成功')
            } else {
                // this.$router.push('/login')
                this.toGetUser();
                window.webkit.messageHandlers.login.postMessage('');
            }
        },

        async addOrder () {
            if (this.userData) {
                const cartId = await fastadd({
                    userId: this.userData.userId,
                    goodsId: this.details.id,
                    number: 1
                })
                // this.$router.push(`/orderDetail?cartId=${cartId}`)
                this.toGetUser();
                window.webkit.messageHandlers.orderDetail.postMessage('?cartId='+cartId);
            } else {
                // this.$router.push('/login')
                this.toGetUser();
                window.webkit.messageHandlers.login.postMessage('');
            }
        },
        async getDetails () {
            let details = await getGoodsDetail(this.id)
            details.price = this.price || details.price
            let img = new Image()
            img.onload = () => {
                this.imgSrc = details.imgSrcArray[0]
                img = null
            }
            img.src = details.imgSrcArray[0]
            this.details = details
        }
    },
    mounted(){
        window.goRouting = this.goRouting;
    }
}
</script>
<style lang="scss">
// img {
//   max-width: 100%;
// }
#details .content {
  div,
  p {
    max-width: 100% !important;
  }
  img {
    max-width: 100% !important;
  }
}
</style>
<style scoped lang="scss">
.img {
  img {
    vertical-align: top;
    width: 100%;
  }
}
.data {
  position: relative;
  padding: 20px 30px;
  background: #fff;
  .pre {
    padding: 0 0 10px;
    font-size: 44px;
    color: #4081ff;
    line-height: 1;
    .nub {
      float: right;
      font-size: 24px;
      color: #999;
      vertical-align: bottom;
      line-height: 2;
    }
  }
  .title {
    margin-bottom: 22px;
    font-size: 32px;
    color: #333;
    line-height: 40px;
  }
  .lable {
    margin-bottom: 12px;
    font-size: 24px;
    color: #999;
    span {
      height: 34px;
      display: inline-block;
      margin-right: 10px;
      padding: 0px 16px;
      border-radius: 20px;
      border: solid 1.5px #999;
      line-height: 34px;
    }
  }
  .content {
    padding: 0 30px;
    margin-top: 20px;
    background: #fff;
  }
}
footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 20px 34px 20px 52px;
  box-sizing: border-box;
  background: #fff;
  .star {
    flex: 1 0;
    line-height: 1;
    text-align: left;
    img {
      width: 52px;
      vertical-align: top;
    }
    p {
      font-size: 24px;
      color: #999;
      line-height: 1;
    }
  }
}
.addCart {
  width: 240px;
  height: 88px;
  margin-right: 24px;
  font-size: 36px;
  background: #f37426;
  color: #fff;
}
.purchase {
  width: 240px;
  height: 88px;
  font-size: 36px;
  color: #fff;
  background: linear-gradient(75deg,#8628fa,#512ffd);;
}
.spike {
  width: 280px;
  height: 88px;
  font-size: 36px;
  color: #fff;
  background: #666;
}
.spike-time {
  float: right;
  font-size: 32px;
  color: #E95558;
  line-height: 1;
  padding: 0px 16px;
}
</style>
